<template>
  <div id="screensaverpage">
    <div class="background_media_layer fixed_media">
      <div class="background_media_layer_overlay fixed_media">
      <!-- Media Layer Overlayed on top of Background Media Layer for an optional additional 9:16 video - width: 2160px, height:3840px, z-index: -10 -->	
      </div> <!-- End .background_media_layer_overlay -->
    </div> <!-- End .background_media_layer -->	
        <div class="main_area">
			<!-- Main Content Area that holds the selectable Tiles of content and the Secondary Content Areas - 12.5% from the Top -->
            <div class="secondary_content_area_wrapper">
                <div class="secondary_content_area">
                <!-- Wrapper for the Secondary Content Area - 0% from Top of Wrapper -->	
                    <div class="secondary_content_widgetarea" id="secondary_content_widgetarea_left">
                    <!-- Left Secondary Content Widget Area - 5% padding left and right width: 26.66%, height:12.5% -->
                        <!-- ** ADD CONTENT OR CODE HERE ** -->	
                
                        <!-- <img src="./images/secondary_content_widgetarea_left.png"/> -->
                    </div>  <!-- End #secondary_content_widgetarea_left -->

                    <div class="secondary_content_widgetarea" id="secondary_content_widgetarea_center">
                    <!-- Center Secondary Content Widget Area - 5% padding left and right width: 26.66%, height:12.5% -->					
                        <!-- ** ADD CONTENT OR CODE HERE ** -->	
    
                    </div> <!-- End #secondary_content_widgetarea_center -->

                    <div class="secondary_content_widgetarea" id="secondary_content_widgetarea_right">
                    <!-- Right Secondary Content Widget Area - 5% padding left and right width: 26.66%, height:12.5% -->
                        <!-- ** ADD CONTENT OR CODE HERE ** -->	
                        
                    </div> <!-- End #secondary_content_widgetarea_right -->
                </div>   <!-- End .secondary_content_area -->		
            </div> <!-- End .secondary_content_area_wrapper -->	
            <!-- Popup Video -->
            <a href="./index.html" class="goBackLink" @click="resetAnalytics"></a>
            <div class="modal" >
                <transition appear name="modal" :key="modalVid">
                    <video autoplay playsinline :muted="$settings.Screensaver_Vids_Muted" ref="modalVideo" :key="modalVid" :loop="loopScreensaverVideo">
                        <source :src="modalVid" type="video/mp4" />
                    </video>
                </transition>
            </div>

      <!-- End .tile_carousel_wrapper -->
    </div>
    <!-- End .main_area -->

      <div class="bottom_area">
        <div class="bottom_content_area_wrapper">
        <!-- Wrapper for the Bottom Content Area - 2.8% from the Bottom -->
          
          <div class="bottom_content_widgetarea" id="bottom_content_widgetarea_left">
          <!-- Left Bottom Content Widget Area - 5% padding left and right width: 26.66% -->
            <!-- ** ADD CONTENT OR CODE HERE ** -->
          </div> <!-- End #bottom_content_widgetarea_left -->
          
          <div class="bottom_content_widgetarea" id="bottom_content_widgetarea_center">
          <!-- Center Bottom Content Widget Area - 5% padding left and right width: 26.66% -->		
            <!-- ** ADD CONTENT OR CODE HERE ** -->
          </div> <!-- End #bottom_content_widgetarea_center -->
          
          <div class="bottom_content_widgetarea" id="bottom_content_widgetarea_right">
          <!-- Right Bottom Content Widget Area - 5% padding left and right width: 26.66% -->				
            <!-- ** ADD CONTENT OR CODE HERE ** -->						
          </div> <!-- End #bottom_content_widgetarea_right -->					
        </div> <!-- End .bottom_content_area_wrapper -->				
