<script>
  import {mapState} from 'vuex';
  export default {
    name: "ScreenSaver",
    components: {
    },
    beforeUnmount() {
      this.$refs.modalVideo.pause();
    },
    methods: {
      // handleClick() {
      //   //memory flush and go to original home page
      //   this.$store.commit("resetAnalytics");
      //   this.$store.commit("setActivePage", "IntroVideoPage");
      //   window.location.href = "./index.html";
      //   return;
      // },
      randomizeVideo(num) {
          this.modalVid = require(`./media/Screensaver Clips/${this.dir[num]}`);
          this.$refs.modalVideo.removeEventListener("ended", () => this.handleEnded(num));
          this.$nextTick(() => {
              this.$refs.modalVideo.addEventListener("ended", () => this.handleEnded(num));
              if (this.activePage === "ScreensaverPage") this.$refs.modalVideo.play();
          })
      },
      integer() {
          if (this.randomized) {
              var num = Math.floor(Math.random() * this.dir.length);
              while (this.videosPlayed.indexOf(num) > -1) {
                  num = Math.floor(Math.random() * this.dir.length);
              }
              return num;
          } else {
              this.counter++
              return this.counter;
          }
          
      },
      handleEnded(num) {
          this.videosPlayed.push(num)
          if (this.videosPlayed.length === this.dir.length) {
              if (this.loopAll) {
                  this.videosPlayed = [];
              } else {
                  return;
              }
          }
          this.$nextTick(() => {
              this.randomizeVideo(this.integer());
          })
      },
      resetAnalytics() {
        this.$store.commit("resetAnalytics");
        this.$store.commit("setActivePage", "IntroVideoPage");
      }
    },
    computed: {
      ...mapState(['carouselTransform', 'carouselPos', 'clickedTilesArray', 'activeDirectory', 'homeDirectory', 'nestedRank', 'activePage'])
    },
    data() {
      return {
        modalVid: null,
        paused: false,
        
        // CHOICE if you want to loop the screen saver video or play it once then return to home screen
        loopScreensaverVideo:false,
        showControls: false,
        timeout: null,
        dir: require.context('./media/Screensaver Clips', false, /\.mp4|.png$/).keys().length > 0 ? 
              require.context('./media/Screensaver Clips', false, /\.mp4|.png$/).keys().map((url) => url.split("./")[1]) : null,
        videosPlayed:[],
        modalVid:null,
        loopAll:true,
        randomized:true,
        counter: -1
      };
    },
    mounted() {
     
        this.randomizeVideo(this.integer());
        // document.getElementById("screensaverpage").addEventListener("click", this.handleClick);
        this.$store.commit("setActivePage", "ScreensaverPage");
        this.$refs.modalVideo.addEventListener("ended", () => this.handleEnded(num));
        this.timeout = setTimeout(() => {
          clearTimeout(this.timeout);
          this.resetAnalytics();
          window.location.href = "./index.html";
        }, this.$settings.Screensaver_Duration_in_Minutes);

        this.randomizeVideo(this.integer());
        // document.getElementById("screensaverpage").addEventListener("click", this.handleClick);
        this.$store.commit("setActivePage", "ScreensaverPage");
        this.$refs.modalVideo.addEventListener("ended", () => this.handleEnded(num));
        this.timeout = setTimeout(() => {
          clearTimeout(this.timeout);
          this.resetAnalytics();
          // window.location.href = "./index.html";
          window.location.reload();
        }, 60000);
        
    },
    created() {
    }
  };
</script>

